export const SET_VISIBLE_CHANNELS = Symbol('SET_VISIBLE_CHANNELS');
export const SET_INPUT_MONTAGE = Symbol('SET_INPUT_MONTAGE');
export const SET_BIPOLAR_MONTAGE = Symbol('SET_BIPOLAR_MONTAGE');
export const SET_DEFAULT_AMPLITUDE = Symbol('SET_DEFAULT_AMPLITUDE');
export const SELECT_CHANNEL = Symbol('SELECT_CHANNEL');

export function setVisibleChannels(channelsList) {
  return {
    type: SET_VISIBLE_CHANNELS,
    payload: channelsList,
  };
}

export function setInputMontage() {
  return {
    type: SET_INPUT_MONTAGE,
  };
}

export function setBipolarMontage() {
  return {
    type: SET_BIPOLAR_MONTAGE,
  };
}

export function setDefaultAmplitude(amplitude) {
  return {
    type: SET_DEFAULT_AMPLITUDE,
    payload: amplitude,
  };
}

export function selectChannel(channel) {
  return {
    type: SELECT_CHANNEL,
    payload: channel,
  };
}

// Define what should escape rollup tree-shaking, since we package preserved
// file structure to npm.
//
// NODE_ENV condition is for consumer app: it should be able to tree-shake
// whatever it wants.
if (process.env.NODE_ENV === 'development') {
  const externalAPI = {
    SET_VISIBLE_CHANNELS,
    SET_INPUT_MONTAGE,
    SET_BIPOLAR_MONTAGE,
    SET_DEFAULT_AMPLITUDE,
    SELECT_CHANNEL,

    setVisibleChannels,
    setInputMontage,
    setBipolarMontage,
    setDefaultAmplitude,
    selectChannel,
  };

  window &&
    window['This function does not exits'] &&
    window['This function does not exits'](externalAPI);
}
